/* #region FAQ Styling */

.faq-container {
    display: flex;
    flex-direction: column;

    max-width: 850px;
    margin: auto;

    /* gap: 1rem; */
}

.faq-card {
    line-height: 1.25;
    display: flex;
    flex-direction: column;

    border-bottom: 1px solid rgb(206, 206, 206);
}

.faq-card a {
    text-decoration: none;
    color: inherit;
}

.faq-card:last-child {
    border: none;
}

.faq-question {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding: 1rem 0;
}

.faq-question>h3 {
    font-size: 1rem;
}

.faq-question>img {
    width: 0.75rem;
    padding: 1rem 0;
}

.faq-answer {
    display: none;
}

.faq-answer.show {
    display: block;
}

.faq-answer p {
    padding-bottom: 1rem;
}

/* #endregion FAQ Styling */
