___CSS_LOADER_EXPORT___.push([module.id, `.slide-show-container {
    min-height: 100lvh;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;
    z-index: -1;
}

/* #region Slideshow Canvas and Images Styling */

.slide-show-canvas {
    width: 100%;
    height: 100%;

    position: absolute;
}

.slide-show-container::before {
    content: "";
    inset: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.35);
    /* backdrop-filter: blur(1px); */
    backdrop-filter: contrast(75%);
}

.slide-show-img {
    width: 100%;
    height: 100%;

    opacity: 0;
    object-fit: cover;
    object-position: center;

    /* transform: scale(1.04); */
    transition: opacity 0.5s ease-in-out;

    position: absolute;
    /* inset: 0; */
    z-index: -1;
}

.slide-show-img.active {
    /* transform: scale(1); */
    opacity: 1;
}

/* #endregion Slideshow Canvas and Images Styling */

/* #region Hero Content Styling */

.hero-content {
    max-width: 90%;
    width: 54rem;

    user-select: none;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
}

.heading {
    will-change: transform;
    transform: translateZ(0);

    color: white;
    text-shadow: black 0 0 15px;

    text-align: center;

    z-index: 0;
}

h1.heading {
    font-size: 3.4rem;
    line-height: 1.15;
    font-family: "PT Serif", serif;

    color: #ffffff;
}

h2.heading {
    font-family: "PT Serif", serif;
    color: #dddddd;
    font-size: 2.25rem;
    font-weight: normal;
}

p.heading {
    color: #ffffff;
    font-size: 1.25rem
}

/* #endregion Hero Content Styling */

/* #region Hero Buttons Styling */

.hero-button {
    padding: 0.5rem 2rem;

    color: #ffffff;
    background-color: rgba(175, 175, 175, 0.5);
    backdrop-filter: blur(5px);
    border: none;
    border-radius: 0.25rem;

    font-size: 1.125rem;

    cursor: pointer;
    text-shadow: black 0 0 15px;
}


/* .hero-button>p {
    position: relative;
}

.hero-button>p::after {
    content: "";
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #0087ca;
    transition: transform 0.25s ease-out;
}

.hero-button>p:hover:after {
    transform: scaleX(1.05);
} */



/* #endregion Hero Buttons Styling */


.data-hero-content {
    /* flex: 1; */
    width: 100%;
    display: grid;
    grid-template-columns: repeat(1, minmax(0px, 1fr));
    color: white;

    bottom: -80%;
    gap: 1rem;
}

.data-hero-content>div {
    padding: 1rem;
    text-align: center;
    backdrop-filter: contrast(60%) blur(5px);
    border-radius: 0.25rem;

}

